import React, { useState } from 'react'
import { Routes, Route, Link, useNavigate } from 'react-router-dom'
import ErrorTest from './components/ErrorTest'
import PerformanceTest from './components/PerformanceTest'
import BehaviorTest from './components/BehaviorTest'

function HomePage() {
  const navigate = useNavigate();
  const [stats, setStats] = useState<any>(null);

  // 获取 Mock 服务器统计信息
  const fetchStats = async () => {
    try {
      const response = await fetch('http://localhost:3001/api/debug/stats');
      const data = await response.json();
      setStats(data);
    } catch (error) {
      console.error('获取统计信息失败:', error);
    }
  };

  // 清空测试数据
  const clearStats = async () => {
    try {
      await fetch('http://localhost:3001/api/debug/clear', { method: 'POST' });
      setStats(null);
      alert('✅ 测试数据已清空');
    } catch (error) {
      console.error('清空数据失败:', error);
    }
  };

  return (
    <div className="test-section">
      <h1>🔍 Mini Sentry SDK 测试面板</h1>

      <div style={{ marginBottom: '20px' }}>
        <h3>📊 数据统计</h3>
        <button className="test-button" onClick={fetchStats}>
          刷新统计
        </button>
        <button className="test-button" onClick={clearStats}>
          清空数据
        </button>

        {stats && (
          <div className="test-status info">
            <strong>收集到的数据:</strong><br />
            错误: {stats.errors} 条 |
            性能: {stats.performance} 条 |
            行为: {stats.behavior} 条 |
            Sourcemap: {stats.sourcemaps} 个
          </div>
        )}
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🧪 测试模块</h3>
        <Link to="/error-test">
          <button className="test-button danger">错误监控测试</button>
        </Link>
        <Link to="/performance-test">
          <button className="test-button">性能监控测试</button>
        </Link>
        <Link to="/behavior-test">
          <button className="test-button">行为追踪测试</button>
        </Link>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🔧 手动操作测试</h3>
        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '点击了手动测试按钮');
          }}
        >
          发送自定义事件
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackMetric('custom_metric', Math.random() * 100);
          }}
        >
          发送自定义指标
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.captureError(new Error('手动触发的测试错误'));
          }}
        >
          手动上报错误
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3>🌐 网络请求测试</h3>
        <button
          className="test-button"
          onClick={() => fetch('/api/test/success').catch(console.error)}
        >
          成功请求
        </button>

        <button
          className="test-button danger"
          onClick={() => fetch('/api/test/404').catch(console.error)}
        >
          404 错误
        </button>

        <button
          className="test-button danger"
          onClick={() => fetch('/api/test/500').catch(console.error)}
        >
          500 错误
        </button>
      </div>

      <div>
        <h3>📋 使用说明</h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6' }}>
          <li>🔴 <strong>错误监控测试</strong>: 触发各种JavaScript错误，测试错误捕获功能</li>
          <li>🔵 <strong>性能监控测试</strong>: 模拟长任务和性能压测，验证性能数据采集</li>
          <li>🟡 <strong>行为追踪测试</strong>: 测试用户交互事件的记录和面包屑轨迹</li>
          <li>📊 查看浏览器 Network 面板观察数据上报</li>
          <li>📝 查看浏览器 Console 面板观察 SDK 日志</li>
          <li>🎯 查看 Mock 服务器终端输出查看接收到的数据</li>
        </ul>
      </div>
    </div>
  );
}

function About() {
  return (
    <div className="test-section">
      <h2>关于页面</h2>
      <p>这是一个用于测试路由跳转和页面停留时间的页面。</p>
      <Link to="/">
        <button className="test-button">返回首页</button>
      </Link>
    </div>
  );
}

function App() {
  return (
    <div>
      {/* 顶部导航 */}
      <nav style={{
        background: '#001529',
        padding: '16px',
        color: 'white',
        marginBottom: '0'
      }}>
        <h1 style={{ margin: 0, fontSize: '18px' }}>
          🔍 Mini Sentry SDK 测试环境
        </h1>
        <div style={{ marginTop: '8px' }}>
          <Link to="/" style={{ color: '#1890ff', marginRight: '16px' }}>首页</Link>
          <Link to="/about" style={{ color: '#1890ff', marginRight: '16px' }}>关于</Link>
          <Link to="/error-test" style={{ color: '#1890ff', marginRight: '16px' }}>错误测试</Link>
          <Link to="/performance-test" style={{ color: '#1890ff', marginRight: '16px' }}>性能测试</Link>
          <Link to="/behavior-test" style={{ color: '#1890ff' }}>行为测试</Link>
        </div>
      </nav>

      {/* 路由内容 */}
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/about" element={<About />} />
        <Route path="/error-test" element={<ErrorTest />} />
        <Route path="/performance-test" element={<PerformanceTest />} />
        <Route path="/behavior-test" element={<BehaviorTest />} />
      </Routes>

      {/* 底部信息 */}
      <footer style={{
        background: '#f0f0f0',
        padding: '16px',
        textAlign: 'center',
        marginTop: '40px',
        fontSize: '12px',
        color: '#666'
      }}>
        <p>
          💡 提示: 打开浏览器开发者工具的 Network 和 Console 面板观察 SDK 行为
          <br />
          📊 Mock 服务器运行在: http://localhost:3001
        </p>
      </footer>
    </div>
  );
}

export default App