import React from 'react'
import ReactDOM from 'react-dom/client'
import { BrowserRouter } from 'react-router-dom'
import App from './App'

// ✅ 修复导入方式 - 使用具名导入
import { Monitor, ErrorBoundary } from 'mini-sentry-sdk'

// 添加网络请求拦截，用于调试
const originalFetch = window.fetch;
window.fetch = function (...args) {
  console.log('🌐 发起请求:', args[0]);
  if (args[1] && args[1].body) {
    console.log('📦 请求体:', args[1].body);
  }
  return originalFetch.apply(this, args)
    .then(response => {
      console.log('✅ 请求成功:', args[0], response.status);
      return response;
    })
    .catch(error => {
      console.error('❌ 请求失败:', args[0], error);
      throw error;
    });
};

console.log('🔧 开始初始化 Mini Sentry SDK...');

// 初始化 SDK
const monitor = new Monitor({
  projectId: '1',
  apiUrl: 'http://192.168.1.161:8080/frontend', // 使用环境变量配置 API URL
  debug: true, // 开启调试模式，便于观察日志
  sampleRate: 1, // 100% 采样，确保所有数据都被收集
  maxBreadcrumbs: 20, // 增加面包屑数量，便于观察
  defaultTags: {
    environment: 'test',
    version: '1.0.0-test',
    testMode: true
  },
  enabledModules: {
    error: true,
    performance: true,
    behavior: true
  }
});

console.log('📊 Monitor 实例创建完成:', monitor);

// 初始化监控
try {
  monitor.init();
  console.log('✅ Monitor 初始化成功');
} catch (error) {
  console.error('❌ Monitor 初始化失败:', error);
}

// 将 monitor 实例挂载到 window 对象，便于在控制台调试
(window as any).monitor = monitor;

// 输出初始化信息
console.log('🚀 Mini Sentry SDK 初始化完成!');

// 检查 SDK 状态
try {
  const status = monitor.getStatus();
  console.log('📊 监控状态:', status);
} catch (error) {
  console.error('❌ 获取状态失败:', error);
}

console.log('🔧 使用 window.monitor 进行调试');

// 测试连接
console.log('🧪 测试服务器连接...');
fetch('http://localhost:3001/health')
  .then(res => res.json())
  .then(data => {
    console.log('✅ 服务器连接正常:', data);

    // 手动测试数据上报
    console.log('🧪 手动测试数据上报...');

    // 测试手动记录事件
    try {
      monitor.trackEvent('navigation', '应用启动', {
        timestamp: Date.now(),
        userAgent: navigator.userAgent,
        url: window.location.href
      });
      console.log('✅ trackEvent 调用成功');
    } catch (error) {
      console.error('❌ trackEvent 调用失败:', error);
    }

    // 测试手动记录指标
    try {
      monitor.trackMetric('app_startup_time', Date.now(), {
        phase: 'initialization'
      });
      console.log('✅ trackMetric 调用成功');
    } catch (error) {
      console.error('❌ trackMetric 调用失败:', error);
    }

    // 测试手动上报错误
    setTimeout(() => {
      try {
        monitor.captureError(new Error('手动测试错误'));
        console.log('✅ captureError 调用成功');
      } catch (error) {
        console.error('❌ captureError 调用失败:', error);
      }
    }, 1000);

  })
  .catch(error => {
    console.error('❌ 服务器连接失败:', error);
    console.log('请检查 Mock 服务器是否运行在 http://localhost:3001');
  });

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <ErrorBoundary
      projectId="TEST-PROJECT-001"
      onError={(error: any) => {
        console.log('🔴 ErrorBoundary 捕获到错误:', error);
        try {
          monitor.captureError(new Error(error.message));
          console.log('✅ ErrorBoundary 错误上报成功');
        } catch (e) {
          console.error('❌ ErrorBoundary 错误上报失败:', e);
        }
      }}
      fallback={
        <div style={{ padding: '20px', textAlign: 'center', color: 'red' }}>
          <h2>⚠️ 应用出现错误</h2>
          <p>ErrorBoundary 已捕获错误，请检查控制台</p>
          <button onClick={() => window.location.reload()}>
            🔄 重新加载
          </button>
        </div>
      }
    >
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </ErrorBoundary>
  </React.StrictMode>
)